import Vue from 'vue'
import Router from 'vue-router'
import HomeComponent from '@/views/Home';
import EditComponent from '@/components/project/Edit';
import CreateComponent from '@/components/project/Create';
import ShowComponent from '@/components/project/Show';
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    { path: '/', redirect: { name: 'home' } },
    { path: '/home', name: 'home', component: HomeComponent },
    { path: '/create', name: 'Create', component: CreateComponent },
    { path: '/edit', name: 'Edit', component: EditComponent },
    { path: '/view', name: 'View', component: ShowComponent },
  ]
});