import { GLTFExporter } from "three/examples/jsm/exporters/GLTFExporter";

export const exporter_settings = {
    trs: false,
    onlyVisible: false,
    binary: true,
    forcePowerOfTwoTextures: true
};

export class Exporter extends GLTFExporter {
    constructor(scope) {
        super();
        this.scope = scope;
        this.settings = exporter_settings;
    }
    async export(data = this.scope.scene, onready=this.download) {
        let gltf;
        
        this.parse(data, function (result) {
            gltf = result;
            const name = data.name;
            onready(gltf, name);
        }, this.settings);
        return Promise.resolve(gltf);
    }
    download (data, name) {
        console.log(data);

        
        let type;
        let ext;
        if (exporter_settings.binary) {
            type = 'application/octet-binary';
            ext = '.glb';
        } else {
            type = 'text/plain';
            ext = '.gltf';
        }

        const blob = new Blob([data], {type: type});
        let link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = name + ext;
        link.click();

    }
}
