<template>
<div class="modal" id="create-project">
  <div class="modal-dialog">
    <div class="modal-content">

      <div class="modal-header">
        <h4 class="modal-title">New</h4>
        <button type="button" class="close" data-dismiss="modal">&times;</button>
      </div>

      <div class="modal-body">
        <form id="create-post-form" @submit.prevent="createProject">
        <div class="form-group col-md-12">
          <label for="title"> Name </label>
          <input type="text" id="name" v-model="name" name="title" class="form-control" placeholder="Enter Name">
        </div>
        <div class="form-group col-md-12">
          <label for="description"> Description </label>
          <input type="text" id="description" v-model="description" name="description" class="form-control" placeholder="Enter Description">
        </div>
      <div class="modal-footer">
        <button class="btn btn-primary" type="submit" data-toggle="modal" data-target="#create-project"> Save </button>
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
      </div>
      </form>
      </div>
    </div>
  </div>
</div>
</template>


<script>
import axios from "axios";
import { server } from "../../helper";
import router from "../../router";
export default {
  data() {
    return {
      name: "",
      description: ""
    };
  },
  methods: {
    createProject() {
      let projectData = {
        name: this.name,
        description: this.description
      };
      this.__submitToServer(projectData);
    },
    __submitToServer(data) {
      axios.post(`${server.baseURL}/project/create`, data).then(data => {
        router.push({name: 'Edit', params: {id: data._id}});
        this.name = "";
        this.description = "";
      });
    },
    navigate() {
      router.go(-1);
    }
  },
  
};
</script>