<template>
<div>
  <label  for="slider-value" class="d-inline float-left"><slot></slot></label>
  <div class="d-inline float-right" v-text="value">
  </div>
  <input id="slider-value"
    ref="slider-value" type="range"
    @input="$emit('input', $event)"
    step="0.01"
    class="slider"
    />
</div>
</template>
<style>
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 1px;
  border-radius: 5px;   
  background: #333333;
  outline: none;
  
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 8px;
  height: 8px;
  border-radius: 50%; 
  background: rgb(35, 37, 44);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 5px;
  border-radius: 50%;
  background: rgb(0, 23, 124);
  cursor: pointer;
}
</style>

<script>
export default {
    name:'slider',
    props:{
        'min': null,
        'max': null,
        'value': null,
    },
    
    mounted() {
      
        this.$refs['slider-value'].min = this.min;
        this.$refs['slider-value'].max = this.max;
        this.$refs['slider-value'].value = this.value;
      
    }
   
}
</script>
