<template >
<div id="engine-view" class="container-fluid" @dblclick="selectObject($event)">
    <div ref="container" ></div>
    <div id="btn-group" class="btn-group ">
        <i class="material-icons btn btn-primary" @click="makeScreenShot()">photo_camera</i>
        <i class="material-icons btn btn-primary" @click="makeMovie()">movie</i>
        <i class="material-icons btn btn-primary" @click="makeScreenShot()">archive</i>
    </div>
</div>
</template>

<style>


#btn-group{
    bottom:20px;
    position: fixed;
    left: calc(50% - calc(48px*1.5));
}

canvas{
  position: fixed;
  top:0px;
  left:0px;
  z-index: -1;
}
</style>

<script>
import Renderer, { renderer } from "../../engine";

import { mapGetters } from 'vuex'

export default {
    
    data() {
        return {
            camera: Object,
            renderer: Object,
            
        };
    },
    created() {

        this.renderer = new Renderer();
        this.camera = this.renderer.camera;
        
        this.setActiveScene(this.renderer.scene);
        mapGetters(['scene']);
    },
    updated() {
        
    },
    mounted () {
        this.$refs.container.appendChild(this.renderer.domElement);
        this.$nextTick(function () {
            this.renderer.setSize(window.innerWidth, window.innerHeight);
            
        });
        this.$store.commit('setRender', { object: this.renderer })
    },
    methods: {
        setActiveScene (scene) {
            this.$store.commit('setScene', { scene: scene })
            this.$store.commit('setObject', { object: null })
        },
        makeMovie() {
            this.renderer.makeMovie();
        },
        makeScreenShot () {
            this.renderer.makeScreenShot();
        },
        imageToCanvas(image) {
            this.renderer.imageToCanvas(image);
        },
        createTextSprite (message, fontSize=15) {
            this.renderer.createTextSprite (message, fontSize);
        },
        selectObject (event) {
            this.renderer.updateRay(event);
        }
        
    }
}
</script>