<template>
<div >
  <engine-view ref="engine-view"/>
  <div class="container">
    <h1> </h1>
    
    <div v-if="projects.length === 0">
        <h2> No projects found at the moment </h2>
    </div>
  </div>
  <div id="container" class="card-deck p-3 mb-3">
    <div class="card  d-flex  shadow" v-for="project in projects" :key="project._id">
        <div class="card-body text-left">
          <h5 class="card-title">{{ project.name }}</h5>
          <p class="card-text">{{ project.description }}</p>
        </div>
        <div class="btn-group btn-sm text-center" style="margin-bottom: 20px;">
          <router-link :to="{name: 'View', params: {id: project._id}}" class="material-icons btn btn-primary">
            play_arrow
          </router-link>
          <router-link :to="{name: 'Edit', params: {id: project._id}}" class="material-icons btn btn-primary">create
          </router-link>
          <button class="material-icons btn btn-primary" @click="deleteProject(project._id)">delete</button>
        </div>
      
    </div>
  </div>
</div>
</template>
<style>
#container {
  top:80px;
}
</style>

<script>
import { server } from "../helper";
import axios from "axios";
import EngineView from '../components//project/EngineView';
import { Scene } from 'three';
export default {
  components: {'engine-view':EngineView},
  data() {
    return {
      projects: [{"assets":[],"_id":"5cdd409691cf3d2444f90b7d","name":"px_02","description":"feature mockup","created_at":"2019-05-16T10:51:02.677Z","__v":0},{"assets":[],"_id":"5ce52dc6eeb9d6716ccf79a9","name":"px_0256","description":"","created_at":"2019-05-22T11:08:54.031Z","__v":0},{"assets":[],"_id":"5cf629ff3a68af598204d97c","name":"px_01","description":"initial release","created_at":"2019-06-04T08:21:19.936Z","__v":0}]
    };
  },
  mounted () {
      this.$refs['engine-view'].setActiveScene(new Scene());
  },
  created() {
    this.fetchProjects();
    
  },
  methods: {
    fetchProjects() {
      axios
        .get(`${server.baseURL}/project/projects`)
        .then(data => (this.projects = data.data));
    },
    deleteProject(id) {
      axios
        .delete(`${server.baseURL}/project/delete?projectID=${id}`)
        .then(data => {
          this.fetchProjects();
        });
    }
  }
};
</script>