<template>
<div v-if="$route.path === '/edit'" class="btn-group">
    <button type="button" class="material-icons btn btn-primary" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">add_circle</button>
    <ul class="dropdown-menu">
        <li class="btn d-flex" @click="createScene()">
            <i class="material-icons">
                folder
            </i>
            Scene
        </li>
        <li class="btn d-flex" @click="createGroup()">
            <i class="material-icons">
                folder
            </i>
            Group
        </li>
        <li class="btn d-flex" data-toggle="modal" data-target="#import-files">
            <i class="material-icons">
                folder
            </i>
            Import
        </li>
        <li class="btn d-flex" @click="createGeo()">
            <i class="material-icons">
                category
            </i>
            Spheres
        </li>
        <li class="btn d-flex" @click="paste()">
            <i class="material-icons">
                category
            </i>
            Paste
        </li>
    </ul>
    <button type="button" class="material-icons btn d-flex">add</button>
</div> 
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import { Group } from "three";
import { renderer } from '../../engine';

export default {
    computed: mapGetters([
      'scene',
      'object'
    ]),
    methods:{
        paste() {
            this.scene.add(this.object.clone());
        },
        createScene() {
            let scene = renderer.createNewScene();
            scene.matrixWorldNeedsUpdate = true;
            this.$store.commit('setScene', { scene: scene })
        },
        
        createGroup() {
            this.add(new Group());
        },
        createGeo(geo=new THREE.SphereBufferGeometry( 1, 40, 40 ), mat=new THREE.MeshStandardMaterial({color:0xffffff * Math.random()}), count=100) {
            
            
            let group = new THREE.Group();
            for ( let i = 0; i < count; i ++ ) {
                mat = new THREE.MeshStandardMaterial({color:0xff00ff * Math.random()});
                mat.roughness = 1 - .5 * Math.random();
                mat.metalness = 1 - .25 * Math.random();
                mat.emissive = mat.color;
                mat.emissiveIntensity = 1 - .25 * Math.random();
                renderer.setEnvMap(mat);
                let mesh = new THREE.Mesh( geo, mat );
                mesh.name = "Sphere_" +i;
                mesh.position.x = ( Math.random() - 0.5 ) * 20;
                mesh.position.y = Math.random() * 5;
                mesh.position.z = ( Math.random() - 0.5 ) * 20;
                mesh.scale.multiplyScalar( .25 -.15 *Math.random());
                
                group.add( mesh );
            
            }
            renderer.scene.add( group );
        },

        add(object) {

            if (this.object) {
                this.object.add(object);
                
            } else {
                this.scene.add(object);
                this.$store.commit('setObject', { object: object })
            }
            
        }
    }    
}
</script>
