<template>
<li v-if="object" :draggable="true">
    <button
      class="btn d-flex"
      @dblclick="toggle"
      @click="setActiveObject"
      

      >
     
        <i v-if="isFolder" @click="toggle" class="material-icons md-24">folder</i>
        <i v-else class="material-icons md-24">{{getType(object)}}</i>
        <slot></slot>
        <i class="material-icons md-24" @click="($props.object.visible = !$props.object.visible)">{{object.visible ? 'visibility' : 'visibility_off'}}</i>
        <i v-if="(object.parent)" class="material-icons md-24" @click="removeObject">delete</i>
        
    </button>
    <ul v-if="isFolder" v-show="isOpen">
      <folder-button
        v-for="(child, index) in object.children"
        :key="index"
        :object="child"
      >{{child.name ===''? child.type : child.name}}</folder-button>
      
    </ul>
</li>

</template>
<script>
export default {
    name:'folder-button',
    props:{
        object:null,
        parent: null
    },
    data: function () {
        return {
            isOpen: false,
            
        }
    },
    methods: {
        toggle: function () {
            if (this.isFolder) {
                this.isOpen = !this.isOpen
            }
        },
        setActiveObject () {
            
            this.$store.commit('setObject', { object: this.$props.object });

        },
        removeObject () {
            
            if (this.object.parent) {
                this.object.parent.remove(this.object)
                this.$store.commit('setObject', { object: null })    
            }
            
        },
        getType(object) {
            switch (object.type) {

                case 'Scene':
                case 'Group':
                    return 'folder';
                    
                case 'Light':
                    return 'wb_incandescent';
                    
                default:
                    return 'category';
                    
            }

        }
    },
    
    computed:{
        isFolder() {
            return this.object.children &&
                this.object.children.length
        },
        
    },
    
}
</script>
