
<template>
<div v-if="scene" id='scene-graph' class="card">
 <ul class="nav">
  <folder-button :object="scene">
    {{scene.name ===''? scene.type : scene.name}}
  </folder-button>
 </ul>
</div>
</template>
<style lang="css">
#scene-graph {
  left:20px;
  
  float:left;
  
}
#scene-graph ul, li {
  list-style-type: none;
  display: block;
  padding:0px 5px;
  margin:0px;
}
</style>

<script>
import { mapGetters, mapActions } from 'vuex';
import FolderButton from "../ui/FolderButton";

export default {
  name: 'scene-graph',
  components: {'folder-button':FolderButton},
  props: {
    item: Object
  },
  data: function () {
    return {
      isOpen: false
    }
  },
  computed: {
    
    ...mapGetters([
      'scene'
    ]),
    
  },
  
  methods: {
    ...mapActions([
      'setActiveObject'
    ]),
    toggle: function () {
      if (this.isFolder) {
        this.isOpen = !this.isOpen
      }
    },
    makeFolder: function () {
      if (!this.isFolder) {
      	this.$emit('make-folder', this.item)
        this.isOpen = true
      }
    }
  }
};
</script>