<template>
<div id='container' >
  <properties-view ref="properties" />
  <scene-graph ref="graph"/>
  <engine-view ref="engine"/>
  <file-import/>
</div>
</template>


<script>
import { server } from "../../helper";
import axios from "axios";
import router from "../../router";

import SceneGraph from './SceneGraph';
import EngineView from './EngineView';
import PropertiesView from './PropertiesView';
import FileImport from "../ui/file-import";
import {Scene} from 'three';

export default {
  name: 'view-project',
  components: {
    'scene-graph': SceneGraph,
    'engine-view': EngineView,
    'properties-view': PropertiesView,
    'file-import': FileImport
  },
  props: {
    scene: {}
  },
  data() {
    return {
      id: 0,
      camera: {},
      
    };
  },
  created() {
    const engine = this.$parent;
    this.id = this.$route.params.id;
    this.getProject();
  },
  
  methods: {
    updateProject() {
      let projectData = {
        name: this.project.name,
        description: this.project.description
      };
      axios
        .put(
          `${server.baseURL}/project/update?projectID=${this.id}`,
          projectData
        );
    },
    getProject() {
      axios
        .get(`${server.baseURL}/project/project/${this.id}`)
        .then(data => {
          this.project = data.data;
          
        });
    },
    navigate() {
      router.go(-1);
    }
  },
  mounted() {
    
  }
};
</script>