import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// root state object.
// each Vuex instance is just a single state tree.
const state = {
    active: {
        scene:Object,
        camera:Object,
        object:Object
    },
    global: {
        renderer:Object,
        
    },
    selected: {
        scene:Object,
        camera:Object,
        object:Object
    }
}

const getters = {
    getScene: (state) => {
        return state.active.scene;
    },
    scene: (state) => {
        return state.active.scene;
    },
    object: (state) => {
        return state.active.object;
    },
    active: (state) => {
        return state.active;
    },
    selected: (state) => {
        return state.active;
    },
    renderer: (state) => {
        return state.global.renderer;
    },

}

const actions = {
    setActiveScene ({ commit }, scene) {
        commit('setScene', { scene: scene });
    },
    setActiveObject ({ commit }, object) {
        commit('setObject', { object: object });
    },
    setRenderer ({ commit }, object) {
        commit('setRender', { renderer: object });
    }

}

const mutations = {
    setScene (state, { scene }) {
        state.active.scene = scene
        
    },
    setObject (state, { object }) {
        state.active.object = object
    },
    setRender (state, { object }) {
        state.global.renderer = object
    },
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})