export class LoadingManager extends THREE.LoadingManager {

    constructor() {

        super();

        this.domElement = document.createElement('div');
        this.domElement.className = 'progress'
        this.bar = document.createElement('div');
        this.bar.className = 'bar'
        this.info = document.createElement('div');
        this.info.className = 'info'
        this.domElement.appendChild(this.bar);
        this.domElement.appendChild(this.info);
        
        //document.body.appendChild(this.domElement);
        
        this.onStart = function( url, itemsLoaded, itemsTotal ) {
            this.bar.style.width = 0 +'%';
            
            this.info.innerText = 'Loading ' + itemsLoaded +' of '+ itemsTotal +' '+ url ;
        }

        this.onProgress = function( url, itemsLoaded, itemsTotal ) {
            
            this.bar.style.width = 100/itemsTotal*itemsLoaded+'%';
        
        }

        this.onLoad = function () {
            this.domElement.style = 'display:none';
            //document.body.removeChild(this.domElement);
            
        }
    
        this.onError = function(url ) {
    
            this.domElement.innerText = ' Sorry, there was a problem with ' + url;
            
        }
    }

}

class ReflexionProbe extends THREE.CubeCamera {

    constructor (near,far, resolution) {
        super(near,far, resolution)
        //this.renderTarget.texture.mapping = TENGINE.CubeReflectionMapping;
        this.renderTarget.texture.generateMipMaps = true;
        this.renderTarget.texture.encoding = THREE.sRGBEncoding;
        this.renderTarget.texture.wrapS = THREE.ClampToEdgeWrapping;
        this.renderTarget.texture.wrapT = THREE.ClampToEdgeWrapping;
        this.renderTarget.texture.image = new Array();
        for (let index = 0; index < 6; index++) {
            this.renderTarget.texture.image[index] = new Image(resolution, resolution);
        }
        
    }

}

export class Environment extends THREE.Scene {

    constructor () {

        super();
        this.sky = new THREE.Sky();
        this.sky.material.uniforms.sunPosition.value.set(-.5,.125,-1.75);
        this.sky.scale.set(2000,2000,2000);
        this.add(this.sky);
        this.intensity = 2.;
    
    }

}

export class EquirectangularMaterial extends THREE.ShaderMaterial {

    constructor (texture) {
        
        let shader = THREE.ShaderLib['equirect'];
        
        let params = {
            uniforms: THREE.UniformsUtils.clone(shader.uniforms),
            vertexShader: shader.vertexShader,
            fragmentShader:shader.fragmentShader,
            side: THREE.BackSide
        };

        super(params);
        this.uniforms.tEquirect.value = texture;
    }

}

class FXAAPass extends THREE.ShaderPass {

    constructor () {

        super(THREE.FXAAShader);
        this.setSize(window.innerWidth, window.innerHeight);
    }

    setSize (width, height) {
        
        this.material.uniforms.resolution.value.set(1/width, 1/height);
    }

}

class CopyPass extends THREE.ShaderPass {

    constructor () {

        super(THREE.CopyShader);
        
    }

}

class AdjustPass extends THREE.ShaderPass {

    constructor() {

        super(BrightnessContrastShader);
        
    }
    brightness = this.uniforms.brightness;
    contrast = this.uniforms.contrast;
    
}

class SaturatePass extends THREE.ShaderPass {

    constructor() {

        super(HueSaturationShader);
        
        
    }
    hue = this.uniforms.hue;
    saturation = this.uniforms.saturation;
    
}

class PostProcessing extends THREE.EffectComposer {

    constructor(renderer) {
        
        super(renderer);
        
    }
    
    addRenderPass(scene, camera, clear=true) {
    
        let pass = new THREE.RenderPass(scene, camera);
        pass.type = 'RenderPass';
        pass.clear = clear;
        pass.renderToScreen = true;
        this.addPass(pass);
    
    }
    
    addShaderPass(shader) {
        let pass = new THREE.ShaderPass(shader);
        pass.type = 'ShaderPass';
        this.addPass(pass);
    
    }
    
    addFXAAPass() {
        let pass = new FXAAPass();
        pass.type = 'FXAAPass';
        this.addPass(pass);
    
    }

    addSSAAPass(scene, camera) {
        let pass = new SSAARenderPass(scene, camera);
        pass.type = 'SSAAPass';
        this.addPass(pass);
    
    }

    addSaturatePass() {
        let pass = new SaturatePass();
        pass.type = 'SaturatePass';
        this.addPass(pass);
    
    }
    addAdjustPass() {
        let pass = new AdjustPass();
        pass.type = 'AdjustPass';
        this.addPass(pass);
    
    }
    addBloomPass() {
    
        let pass = new BloomPass( 1.5,    // strength
            25,   // kernel size
            4,    // sigma ?
            1024,  // blur render target resolution
        );
        pass.type = 'BloomPass';
        pass.renderToScreen = true;
        this.addPass(pass);
        
    
    }

    addSSAOPass( scene, camera, width, height ) {
        var pass = new SSAOPass( scene, camera, width, height );
        pass.kernelRadius = 16;
        pass.type = 'SSAOPass';
        pass.output = 3;

        this.addPass(pass);
    }

    addCopyPass() {
    
        let pass = new CopyPass();
        pass.renderToScreen = true;
        pass.type = 'CopyPass';
        this.addPass(pass);
    
    }

}

let RendertargetOptions = {
    encoding: THREE.LinearEncoding,
    type: THREE.FloatType
    //stencilBuffer: false
};

export default class Renderer extends THREE.WebGLRenderer {
    
    constructor(canvas) {
        if(renderer) {return renderer;}
        if (!canvas) {
        
            canvas=document.createElement('canvas');
            
            canvas.width = window.innerWidth;
            canvas.height = window.innerHeight;
            
        }
            
        let context = canvas.getContext( 'webgl2' );
        super({alpha:true, antialias:true, canvas:canvas, context:context, precision:"highp"});
        renderer = this;
        
        this.physicallyCorrectLights = true;
        this.autoClear = false;
        this.composers = new Array();
        this.extensions.get( 'OES_texture_float_linear' );
        //this.extensions.get( 'EXT_texture_filter_anisotropic' );

        this.gammaInput = false;
        this.gammaOutput = true;
        this.gammaFactor = 1;
        this.toneMapping = THREE.Uncharted2ToneMapping;
        this.setRenderTarget(this.createRenderTarget());
        this.setPixelRatio(window.devicePixelRatio);
        
        //
        this.environment = new Environment();
        this.envMap = new THREE.CubeTexture();
        this.probe = new ReflexionProbe(.1, 1000, 512);
        this.lightProbe = new THREE.LightProbe();
        
        this.scene = new THREE.Scene();
		this.scene.add( this.lightProbe );
        this.camera = new THREE.PerspectiveCamera( 70, renderer.domElement.clientWidth/renderer.domElement.clientHeight, .1, 10000 );
        this.camera.position.set(5,1.8,5)
        this.camera.lookAt(this.scene.position);
        this.controls = new THREE.OrbitControls(this.camera, this.domElement);
        //this.controls.addEventListener( 'change', this.update );
		this.controls.minDistance = 1;
        this.controls.maxDistance = 10;
        this.controls.enablePan = true;
        this.controls.enableDamping = true;
        this.controls.damping = 1.2;
        
        this.controls.autoRotateSpeed = .05;
        
        let transformControl = new THREE.TransformControls( this.camera, this.domElement );
        window.addEventListener( 'click', this.updateRay );
        transformControl.addEventListener( 'dragging-changed', function ( event ) {
            renderer.controls.enabled = ! event.value;
        });
        this.scene.add( transformControl );
        transformControl.enabled = true;
        this.ray = new THREE.Raycaster();
        this.mouse = new THREE.Vector2();
        
        this.helper = new THREE.Group();
        this.helper.name = 'Helper';
        
        let helper = new THREE.GridHelper( 10, 20 );
        helper.name = 'Grid'
        helper.position.y = 0;
        helper.material.opacity = 0.55;
        helper.material.transparent = true;
        this.helper.add(helper);

        this.scene.add(this.helper);
        this.isWebGL2 = true;
        

        this.postprocessing = new PostProcessing(this);
        this.postprocessing.addRenderPass(this.environment, this.camera, true);
        
        this.postprocessing.addRenderPass(this.scene, this.camera, false);
        
        //this.postprocessing.addSSAAPass(this.scene, this.camera)
        //
        this.postprocessing.addAdjustPass();
        this.postprocessing.addSaturatePass();
        this.postprocessing.addBloomPass();
        this.postprocessing.addFXAAPass();
        this.postprocessing.addCopyPass();
        /* 
        
        
        this.postprocessing.addCopyPass(); */
        
        //this.composers.push(bg_composer);
        this.composers.push(this.postprocessing);
        console.log(this)
        this.resize();
        
        window.addEventListener( 'resize', this.resize, false );
        
        this.scene.add(this.lightProbe);
        this.generateFilteredMipMap();
        
        this.setAnimationLoop(this.update);
        
    }

    renderVR() {
        renderer.render( renderer.scene, renderer.camera );
    }

    createRenderTarget () {

        const size = this.getDrawingBufferSize( new THREE.Vector2() );
            
        if (this.isWebGL2) {
             const rt = new THREE.WebGLMultisampleRenderTarget(size.width, size.height, RendertargetOptions);
             rt.samples = 8;
            return 
        } else {
            return new THREE.WebGLRenderTarget(size.width, size.height, RendertargetOptions);
            
        }
        
    }

    async generateFilteredMipMap () {

        this.probe.update(this, this.environment);
        let generator = new THREE.PMREMGenerator(this.probe.renderTarget.texture);
        generator.update(this);
        let packer = new THREE.PMREMCubeUVPacker(generator.cubeLods);
        packer.update(this);
        this.envMap = packer.CubeUVRenderTarget.texture;
        
        this.lightProbe.copy( THREE.LightProbeGenerator.fromCubeTexture( this.probe.renderTarget.texture ) );
        
        generator.dispose();
        packer.dispose();

        this.traverseMaterials(this.scene, this.setEnvMap);
        
    }

    setEnvMap (material) {

        if(material instanceof THREE.MeshStandardMaterial || material instanceof THREE.MeshPhysicalMaterial) {

            material.envMap = renderer.envMap;
            renderer.setEnvMapIntensity(material);
        }
    }
    
    async setEnvironmentIntensity(value) {

        renderer.environment.intensity = value;
        //renderer.generateFilteredMipMap ();
        renderer.traverseMaterials(renderer.scene, renderer.setEnvMapIntensity);
        renderer.update();
    }

    setEnvMapIntensity (material) {

        material.envMapIntensity = renderer.environment.intensity;

    }

    async traverseMaterials (scene, callback) {

        scene.traverse(child => {
            
            if (child instanceof THREE.Mesh) {

                callback(child.material);
                
            }
        });

    }

    resize () {
   
        let width = window.innerWidth, height =  window.innerHeight;
        renderer.camera.aspect = width/height;
        renderer.camera.updateProjectionMatrix();
        renderer.setSize(width, height);
    
        for (let index = 0; index < renderer.composers.length; index++) {
            const element = renderer.composers[index];
            element.setSize(width, height);
        }
        
        renderer.update();

    }

    update() {
        
        let delta = clock.getDelta();
        if(renderer.controls.enableDamping || renderer.controls.autoRotate ){
            renderer.controls.update();
        }
        
        for (let index = 0; index < renderer.composers.length; index++) {
            let element = renderer.composers[index];
            element.render(delta);
        }
    }

    makeScreenShot = (width=1920, height=1080) => {
        
        renderer.setSize(width, height);
        renderer.resize()
        
        renderer.domElement.toBlob(function(blob){
            let link = document.createElement('a')
            link.href = URL.createObjectURL(blob);
            let date = new Date();
            date = date.toLocaleDateString();
            link.download = 'Scene_' + date +'.jpeg';
            link.click();
        },'image/jpeg',.95);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.resize();
    }

    updateRay = (event) => {
        
        this.mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
        this.ray.setFromCamera(renderer.mouse, renderer.camera);
        let intersects = this.ray.intersectObjects(renderer.scene.children);

        if ( intersects.length > 0 ) {
            let intersect = intersects[ 0 ];
            console.log('intersect',intersect)
        }
        console.log(this.mouse, this.ray, event);
    }

    export = () => {

    }

    updateScene(scene) {

        renderer.traverseMaterials(scene, renderer.setEnvMap);
        renderer.update();
        scene.traverse(child => {
            console.log(child, child.parent)
        });
    }

    setScene(scene) {

        renderer.postprocessing.passes[1].scene = scene;
        renderer.scene = scene;
        renderer.camera.lookAt(scene.position);
        renderer.controls.update();

        renderer.updateScene(scene);

    }

    createNewScene() {
        let scene = new Root();
        this.setScene(scene);
        return scene;

    }

    makeMovie() {

        
    }

    createTextSprite (message, fontSize=15) {
        const font = fontSize + 'px sans-serif';
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        context.font = font;
        canvas.width = context.measureText(message).width;
        canvas.height = fontSize * 1.4;
        context.font = font;
        context.fillStyle = 'rgba(0, 0, 0, 1)';
        context.fillText(message, 0, fontSize);
        const texture = new THREE.Texture(canvas)
        texture.minFilter = THREE.LinearFilter;
        texture.needsUpdate = true;
        const spriteMaterial = new THREE.SpriteMaterial({ map: texture });
        const sprite = new THREE.Sprite(spriteMaterial);
        sprite.scale.x = canvas.width / canvas.height;
        return sprite;
    }

    imageToCanvas ( image, x=0, y=0 ) {
        var width = image.width;
        var height = image.height;
        var canvas = document.createElement( 'canvas' );
        canvas.width = width;
        canvas.height = height;
        var context = canvas.getContext( '2d' );
        context.drawImage( image, x, y, width, height );
        return canvas;
    }

}