<template>
<div id='properties-view' class="shadow rounded">
  <i class="material-icons btn btn-primary float-right" data-toggle="collapse" data-target="#renderer">settings</i>
      
  <div id="renderer" class="card collapse"  p-3 mb-2>
    <div class="card-body" v-if="object">
      <input type='text' v-model="object.name"/>
      <toggle-button
        @click="(object.visible = !object.visible)"
        v-bind:prop="object.visible">
        Visible
      </toggle-button>
      <div v-if="object.isMesh" >
        <div v-if="object.material.type === 'MeshStandardMaterial'" >
          <input type="range" step="0.01" min="0" max="1" class="slider" v-model="object.material['metalness']"/>
          <input type="range" step="0.01" min="0" max="1" class="slider" v-model="object.material['roughness']"/>
          Color:<color-picker value="object.material.color"/>
        </div>
        <toggle-button
          @click="(object.material.transparent = !object.material.transparent)"
          v-bind:prop="object.material.transparent">
          Transparent
        </toggle-button>
        <input v-if="object.material.transparent" type="range" step="0.01" min="0" max="1" class="slider" v-model="object.material['opacity']"/>
          
      </div>
    </div>
    <div class="card-body">
      <div v-if="renderer">
        <slider
        :min="1"
        :max="4"
        @input="(renderer.toneMappingExposure = $event.target.value)"
        :value="renderer.toneMappingExposure"
        >Exposure</slider>
        <slider
        :min="1"
        :max="4"
        @input="(renderer.toneMappingWhitePoint = $event.target.value)"
        :value="renderer.toneMappingWhitePoint"
        >Whitepoint</slider>
        <slider
        :min="1"
        :max="4"
        @input="setEnvironmentIntensity($event.target.value)"
        :value="renderer.environment.intensity"
        >Environment Intensity</slider>
      </div>
      <div>
        <toggle-button v-if="renderer.helper" @click="(renderer.helper.visible = !renderer.helper.visible)" v-bind:prop="renderer.helper.visible">
          Grid
        </toggle-button>
        <toggle-button
          v-if="renderer.controls"
          @click="(renderer.controls.autoRotate = !renderer.controls.autoRotate)"
          v-bind:prop="renderer.controls.autoRotate">
          Auto Rotate
        </toggle-button>
      </div>
      
      </div>
      </div>
      
    </div>
  </div>

</div>
</template>
<style lang="css">
#properties-view {
  position: relative;
  margin: 20px;
  float:right;
  top:80px;
}
</style>

<script>
import { mapGetters } from 'vuex';
import ToggleButton from "../ui/ToggleButton";
import ColorPicker from "../ui/ColorPicker";
import Slider from '../ui/Slider';

export default {
  name: 'properties-view',
  components: {
    'slider':Slider,
    'toggle-button':ToggleButton,
    'color-picker':ColorPicker
    },
  
  created() {
    this.id = this.$route.params.id;
    
  },
  mounted() {
    
  },
  computed: mapGetters([
      'object',
      'renderer'
  ]),
  methods: {
    toggle(value) {
      value != value;
      console.log(value);
    },
    setEnvironmentIntensity(value) {
      this.renderer.setEnvironmentIntensity(value);
    }
    
  }
};
</script>