<template>
<div class="modal" id="export-files">
  <div class="modal-dialog">
    <div class="modal-content">

      <div class="modal-header">
        <h4 class="modal-title">Export</h4>
        <button type="button" class="close" data-dismiss="modal">&times;</button>
      </div>

      <div class="modal-body">
        <div v-if="exporter.settings" class="form-group col-md-12">
          <label for="title"> Name </label>
          <input type="text" id="name" v-model="scene.name" name="title" class="form-control" placeholder="Enter Name">
          <label for="binary"> Binary </label>
          <input type="checkbox" id="binary" v-model="exporter.settings.binary" name="binary" class="form-control" placeholder="Enter Name">
        </div>
      </div>

      <div class="modal-footer">
        <a class="btn" @click="this.export" data-dismiss="modal">Export</a>
      </div>

    </div>
  </div>
</div>
</template>
<style lang="css">
  
</style>

<script>

import { mapGetters } from 'vuex';
import {Exporter} from "../../../src/Exporter";

export default {
  name: 'file-import',
  data() {
    return {
      id: 0,
      exporter: {},
      file:null
    };
  },
  created() {
    this.id = this.$route.params.id;
  },
  mounted() {
    this.exporter = new Exporter(this);
  },
  computed: mapGetters([
      'scene'
  ]),
  methods: {
    export() {
       this.exporter.export(this.scene);
    },
    
  }
};
</script>