import { LoadingManager, THREE, renderer, EquirectangularMaterial } from './engine';
require('three/examples/js/loaders/RGBELoader');
require('three/examples/js/loaders/EXRLoader');
require('three/examples/js/loaders/KTXLoader');
require('three/examples/js/loaders/FBXLoader');
require('three/examples/js/loaders/ColladaLoader');
require('three/examples/js/loaders/OBJLoader');
require('three/examples/js/loaders/STLLoader');
require('three/examples/js/loaders/GLTFLoader');
require('three/examples/js/loaders/GLTFLoader');
require('three/examples/js/loaders/DRACOLoader');
THREE.DRACOLoader.setDecoderPath('../draco/');
export class Importer extends THREE.FileLoader {
    constructor(scope) {
        super();
        this.scope = scope;
        this.manager = new LoadingManager();
        this.hdr = new THREE.RGBELoader(this.manager);
        this.hdr.setType(THREE.FloatType);
        this.exr = new THREE.EXRLoader(this.manager);
        this.ktx = new THREE.KTXLoader(this.manager);
        this.fbx = new THREE.FBXLoader(this.manager);
        this.dae = new THREE.ColladaLoader(this.manager);
        this.obj = new THREE.OBJLoader(this.manager);
        this.stl = new THREE.STLLoader(this.manager);
        this.draco = new THREE.DRACOLoader(this.manager);
        this.gltf = new THREE.GLTFLoader(this.manager);
        this.gltf.setDRACOLoader(this.draco);
    }
    async import(files) {
        let self = this;
        this.prepareLoadingManager(files);
        for (let i = 0; i < files.length; i++) {
            let file = files[i];
            let filename = file.name;
            let extension = filename.split('.').pop().toLowerCase();
            let reader = new FileReader();
            switch (extension) {
                case 'hdr':
                    self.hdr.load(URL.createObjectURL(file), function (texture, textureData) {
                        texture.name = filename;
                        texture.minFilter = THREE.NearestFilter;
                        texture.magFilter = THREE.NearestFilter;
                        texture.flipY = true;
                        renderer.environment.sky.material = new EquirectangularMaterial(texture);
                        renderer.generateFilteredMipMap();
                        renderer.update();
                    });
                    break;
                case 'exr':
                    reader.addEventListener('load', function (event) {
                        let result = self.hdr.parse(event.target.result);
                        result.name = filename;
                        //scene.add(result);
                    }, false);
                    reader.readAsArrayBuffer(file);
                    break;
                
                case 'fbx':
                    reader.addEventListener('load', function (event) {
                        let result = self.fbx.parse(event.target.result);
                        result.name = filename;
                        renderer.setScene(result);
                        self.scope.$store.commit('setScene', { scene: result });
                    }, false);
                    reader.readAsArrayBuffer(file);
                    break;

                case 'dae':
                    reader.addEventListener('load', function (event) {
                        let result = self.dae.parse(event.target.result);
                        result.scene.name = filename;
                        
                        renderer.setScene(result.scene);
                        self.scope.$store.commit('setScene', { scene: result.scene });
                    }, false);
                    reader.readAsText(file);
                    break;

                case 'obj':
                    reader.addEventListener('load', function (event) {
                        let result = self.obj.parse(event.target.result);
                        result.name = filename;
                        let scene = new THREE.Scene();
                        scene.add(result);
                        renderer.setScene(scene);
                        self.scope.$store.commit('setScene', { scene: scene });
                    }, false);
                    reader.readAsText(file);
                    break;

                case 'stl':
                    reader.addEventListener('load', function (event) {
                        let result = self.stl.parse(event.target.result);
                        result.sourceType = 'stl';
                        result.sourceFile = filename;
                        let material = new THREE.MeshStandardMaterial();
                        let mesh = new THREE.Mesh(result, material);
                        mesh.name = filename;
                        let scene = new THREE.Scene();
                        scene.add(mesh);
                        renderer.setScene(scene);
                        self.scope.$store.commit('setScene', { scene: scene });
                    }, false);
                    if (reader.readAsBinaryString !== undefined) {
                        reader.readAsBinaryString(file);
                    }
                    else {
                        reader.readAsArrayBuffer(file);
                    }
                    break;

                case 'gltf':
                    reader.addEventListener('load', function (event) {
                        self.gltf.parse(event.target.result, '', function (gltf) {
                            gltf.scene.name = filename;
                            renderer.setScene(gltf.scene);
                            self.scope.$store.commit('setScene', { scene: gltf.scene });
                            return Promise.resolve(gltf);
                        });
                    }, false);
                    reader.readAsText(file);
                    break;
                case 'glb':
                    reader.addEventListener('load', function (event) {
                        self.gltf.parse(event.target.result, '', function (gltf) {
                            gltf.scene.name = filename;
                            renderer.setScene(gltf.scene);
                            self.scope.$store.commit('setScene', { scene: gltf.scene });
                            return Promise.resolve(gltf);
                        });
                    }, false);
                    reader.readAsArrayBuffer(file);
                    break;
                default:
                    break;
            }
        }
    }
    prepareLoadingManager(files) {
        const map = {};
        for (let i = 0; i < files.length; i++) {
            const file = files[i];
            map[file.name] = file;
        }
        this.manager.setURLModifier((url) => {
            let file = map[url];
            if (file) {
                return URL.createObjectURL(file);
            }
            return url;
        });
    }
}
