<template>
<div id='container' >
  <engine-view ref="engine"/>
</div>
</template>


<script>
import { server } from "../../helper";
import axios from "axios";
import router from "../../router";

import EngineView from './EngineView';

export default {
  name: 'view-project',
  components: {'engine-view':EngineView},
  props: {
    scene: {}
  },
  data() {
    return {
      id: 0,
      camera: {},
      
    };
  },
  created() {
    const engine = this.$parent;
    console.log(engine);
  },
  
  updated() {
    
  },
  
  methods: {
    showProject() {
      let projectData = {
        name: this.project.name,
        description: this.project.description
      };
      axios
        .put(
          `${server.baseURL}/project/update?projectID=${this.id}`,
          projectData
        );
    },
    getProject() {
      axios
        .get(`${server.baseURL}/project/project/${this.id}`)
        .then(data => (this.project = data.data));
    },
    
    navigate() {
      router.go(-1);
    }
  }
};
</script>